/////////////////////////
// TYPOGRAPHY SYSTEM
/////////////////////////

- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- Font weights
Default: 400
Medium: 500
Semi-bold: 600
Bold: 700

- Line heights
Default: 1
Small: 1.05
Medium: 1.2
Paragraph default: 1.6
Large: 1.8

- Letter spacing
0.5px
0.75px

/////////////////////////
// BORDER-RADIUS
/////////////////////////

Default: 9px
Medium: 11px

/////////////////////////
// WHITESPACE
/////////////////////////

- Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128
*/

:root {
  --primary-white: #ddd;
  --primary-white--light: #eee;
  --primary-white--darker: #bbb;
  --primary-white--highlight: #d9d9d9;

  --primary-black: #333;
  --primary-black--light: #686868;

  --primary-red: #c92a2a;
  --primary-red--light: #efbfbf;
  --primary-red--dark: #a12222;
  --primary-red--darker: #791919;

  --border-radius: 9px;
}

@font-face {
  font-family: "Outfit";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  font-family: "Outfit", sans-serif;
  color: var(--primary-white);
  line-height: 1;
  font-weight: 400;
  background-color: var(--primary-black);
}

a:link,
a:visited {
  color: var(--primary-white);
  text-decoration: none;
  transition: color 300ms;
}

a:hover,
a:active {
  color: var(--primary-white);
}

a.inline-link {
  text-decoration: underline;
}

/* //////////////////////////////////// */
/* REUSABLE */
/* //////////////////////////////////// */

.container {
  max-width: 130rem;
  margin: 0 auto;
  padding: 3.2rem;
}

.grid {
  display: grid;
}

.grid--1-cols {
  grid-template-columns: repeat(1, 1fr);
}

.grid--2-cols {
  grid-template-columns: repeat(2, 1fr);
}

.center-text {
  text-align: center;
}

.relative {
  position: relative;
}

.chessboard {
  background-image: linear-gradient(rgba(0, 0, 0, 0.85), rgba(51, 51, 51, 0.85)),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-position: center;
}

.icon-container {
  position: absolute;
  display: flex;
  align-items: center;

  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.icon {
  /* fill: var(--primary-red);
  stroke: var(--primary-black); */
  height: 2.4rem;
  width: 2.4rem;
}

.alert {
  position: fixed;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;

  max-width: 40rem;
  text-align: center;
  padding: 1.2rem 2.4rem;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.alert--success {
  background-color: #37b24d;
}

.alert--error {
  background-color: var(--primary-red);
}

.alert-heading {
  font-size: 2.4rem;
}

.form-fieldset {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;

  margin-bottom: 2.4rem;
  border: none;
}

.card {
  padding: 3.2rem 4.8rem;
  background-color: var(--primary-white--light);
  border: none;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.heading {
  font-weight: 600;
  letter-spacing: 1.2px;
  margin-bottom: 2.4rem;
}

.heading--primary {
  font-size: 6.2rem;
}

.heading--secondary {
  font-size: 4.8rem;
}

.heading--tertiary {
  font-size: 3.2rem;
}

.section-header {
  color: var(--primary-white--darker);
  font-size: 2.4rem;
  margin-bottom: 9.6rem;
}

.content {
  font-size: 2rem;
  letter-spacing: 0.5px;
}

button {
  color: var(--primary-white);
  font-family: "Outfit", sans-serif;
}

.btn {
  font-size: 2.4rem;
  letter-spacing: 0.5px;
  background-color: var(--primary-white);

  border: none;
  border-radius: 10px;
  padding: 1.4rem 3.2rem;

  cursor: pointer;
  transition: background-color 300ms;
}

.btn:link,
.btn:visited {
  color: var(--primary-black);

  transition: background-color 300ms;
}

.btn:hover,
.btn:active {
  background-color: var(--primary-white--light);
}

.btn--cta {
  background-color: var(--primary-red);
  font-weight: 500;
}

.btn--cta:link,
.btn--cta:visited {
  color: var(--primary-white);

  transition: background-color 300ms;
}

.btn--cta:hover,
.btn--cta:active {
  color: var(--primary-white);
  background-color: var(--primary-red--dark);
}

.input-container {
  display: flex;
  flex-direction: column;
}

.input-label,
.field-label {
  color: var(--primary-black--light);
  font-size: 1.2rem;
  letter-spacing: 1px;

  margin: 0 0 3px 3px;
}

.input-field {
  font-size: 2rem;
  font-family: "Outfit", sans-serif;
  letter-spacing: 0.8px;

  width: 100%;
  border: 1px solid var(--primary-white--darker);
  border-radius: 10px;
  padding: 0.8rem 1.6rem;
}

.hidden {
  display: none !important;
}

/* //////////////////////////////////// */
/* GENERAL */
/* //////////////////////////////////// */

.logo {
  width: 4rem;
}

/* //////////////////////////////////// */
/* Data Visualizations (D3) */
/* //////////////////////////////////// */

.section-dashboard {
  background-color: var(--primary-white);
}

.canvas-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 3.2rem 0;
}

.canvas-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  max-width: 100rem;
  padding: 3.2rem 0;
  background-color: var(--primary-white--light);
  border: none;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.canvas {
  position: relative;
}

.canvas-heading {
  margin-bottom: 3.2rem;
}

.graph-options {
  display: flex;
  justify-content: space-around;

  width: 100%;
}

.options-group {
  position: relative;

  display: flex;
}

.options-group::after {
  content: attr(data-title);
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);

  font-size: 1.2rem;
  font-style: italic;
  letter-spacing: 1px;
}

.options-group button:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.options-group button:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.btn--option {
  color: #333;
  font-size: 1.6rem;
  border: 1px solid var(--primary-white);
  border-radius: 0;

  transition: none;
}

.btn--option.active {
  color: var(--primary-white);
  background-color: var(--primary-black);
}

.axis text {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  letter-spacing: 0.02rem;
  stroke: var(--primary-black);
  fill: var(--primary-black);
}

.legend-rect {
  width: 2rem;
  height: 2rem;
}

.legend-text {
  font-family: "Outfit", sans-serif;
  font-size: 1.1rem;
  letter-spacing: 0.2rem;
  stroke: var(--primary-black);
  fill: var(--primary-black);
}

.tooltip-container {
  position: absolute;
  display: none;

  color: var(--primary-white);
  padding: 12px 24px;
  width: max-content;
  transform: translateY(-100%);
  border: 2px solid var(--primary-white);
  border-radius: var(--border-radius);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  background-color: var(--primary-black);
}

.tooltip {
  position: relative;

  height: 100%;
}

.tooltip::before {
  position: absolute;
  left: 20px;
  bottom: -20px;
  transform: rotate(45deg);

  content: "";
  width: 18px;
  height: 18px;

  background-color: var(--primary-black);
}

.tooltip-content .tip-header {
  font-size: 2rem;
  margin-bottom: 0.4rem;
}

.tooltip-content .tip-subheader {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.tooltip-content .tip-body {
  font-size: 2.4rem;
}

.legend-container {
  display: flex;
  gap: 40px;
}

.key-group {
  display: flex;
  align-items: center;
  column-gap: 6px;
}

.legend-shape--rect {
  height: 20px;
  width: 20px;
}

.legend-shape--circle {
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

.legend-text {
  color: var(--primary-black);
  font-size: 1.6rem;
}

.axis-title {
  position: absolute;

  font-size: 1.2rem;
  color: #888;
  letter-spacing: 1px;
}

.axis-title--x {
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
}

.axis-title--y {
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(-90deg);
}
